body {
    margin: 0;
    padding: 0;
    /* background: linear-gradient(120deg, #e07ec1, #d20651); */
    height: 100vh;
    /* overflow: hidden; */
}

.center-login {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  background: white;
  border-radius: 10px;
  /* background: linear-gradient(120deg, #e07ec1, #bf7d95); */
  background: linear-gradient(50deg, #ffffff, #fafafa);
  padding-top: 15px;
  border-radius: 15px;

}

.center-login h1 {
    text-align: center;
    padding: 0 0 20px 0;
    border-bottom: 0.5px solid #dadada;
}

.center-login form {
    padding: 0 40px;
    box-sizing: border-box;
}

form .txt_field {
    position: relative;
    border-bottom: 2px solid #dadada;
    margin: 30px 0;
}

.txt_field input {
   width: 100%;
   padding: 0 5px;
   height: 40px;
   font-size: 16px;
   border: none;
   background: none;
   outline: none; 
}

.txt_field label {
    position: absolute;
    top: 50%;
    left: 5px;
    color: black;
    transform: translateY(-50%);
    font-size: 16px;
    pointer-events: none;
    transition: 0.5s;
}

.txt_field span::before {
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    width: 0%;
    height: 2px;
    background: red;
    transition: 1s;
}

.txt_field input:valid ~ label,
.txt_field input:hover ~ label {
    top: -5px;
    color: black;
}

.txt_field input:valid ~ span::before,
.txt_field input:hover ~ span::before {
    width: 100%;
}

.login-btn {
    width: 100%;
    height: 50px;
    border: 1px solid;
    background: rgb(218, 40, 40);
    border-radius: 25px;
    font-size: 18px;
    color: #e9f4fb;
    font-weight: 700;
    cursor: pointer;
    outline: none;
}

.login-btn:hover {
    border-color: rgb(0, 0, 0);
    transition: 0.5s;
}

.signup_link {
    margin: 30px 0;
    text-align: center;
    font-size: 16px;
    color: black;
}

.signup_link a {
    color: black;
    text-decoration: underline;
}

.signup_link a:hover {
    text-decoration: underline;
}

.logintitle{
    padding-top: 10px;

}
