.card-container {
    margin: 1em;
    width: 300px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    transition: 0.2s;
    animation: ease-in;
    background-color: rgb(255, 255, 255);
    border-radius: 15px;
    
}

.card-container:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 15px 0px;
}

.image-container img {
    overflow: hidden;
    height: auto;
    width: 100%;  
}

.card-content {
    margin: 1rem;
    margin-top: 0.5rem; 
}

.card-title {
    margin-bottom: 0.5rem;
    text-align: center;
}

.card-container a {
    text-decoration: none;
    color: black;
}

